// Right part of the split view
// Visible only in the details ui-view
body.router--work-packages-partitioned-split-view-details,
body.router--work-packages-partitioned-split-view-new

  .work-packages-partitioned-page--content-right
    overflow: auto
    position: relative
    border-left: 2px solid #eee
    border-top: 2px solid #eee
    padding: 0

    // Will eventually be overridden by the resizer
    flex-basis: 580px

.work-packages--details
  height: 100%
  position: relative
  width: 100%
  // Min-width is actually 530px but the border already needs 2px
  min-width: 528px

  @media only screen and (max-width: 1280px)
    @at-root
      .detailsViewMenuItem
        display: none

  .op-tab-row
    padding-left: 20px

.work-packages--details-toolbar-container
  position:   absolute
  bottom:     0
  height:     55px
  width:      100%
  background: #F8F8F8
  border:     1px solid #eeeeee
  padding:    0 1rem 10px

  @media print
    display: none

  .button
    margin: .5rem .5rem 0 0

.work-packages--details-content
  display:         flex
  flex-direction:  column
  position:        absolute
  top:             50px
  bottom:          55px
  width:           100%
  overflow:        hidden

  &.-create-mode
    padding: 0 5px 10px 20px

// Fix height of subject row
.work-packages--subject-element,
.work-packages--details--subject .inline-edit--field
  font-size: 1.125rem
  font-weight: bold

.work-packages--details--subject
  overflow: hidden

  .inline-edit--field
    height: 38px
    line-height: 36px

  // Style edit field to look like the display field.
  // Thus we avoid a visual jump when editing the subject.
  &.-active input
    height: 34px
    line-height: 34px
    padding: 5px 0 5px 5px
    font-size: 18px

.work-packages--details-form
  display: flex
  flex-direction: column
  height: 100%
  overflow: hidden

.work-packages--details-header
  display: flex
  padding: 0 5px 10px 20px
  border-bottom: 1px solid #ddd
  margin-bottom: 10px

.work-packages--details-header-left
  display: flex
  flex-direction: column
  margin-right: 4px
  max-width: 100%
  flex-basis: 100%
  flex-shrink: 1
  flex-grow: 0

.work-package-details-tab
  overflow-y: auto
  overflow-x: hidden
  flex-grow: 1
  padding: 0 8px 10px 20px
  @include styled-scroll-bar

.work-packages--breadcrumb
  margin-bottom: 4px

.work-packages--type-selector
  flex-shrink: 0

.work-packages--details
  .work-packages--attachments
    margin-bottom: 25px

.work-package--new-state
  margin-bottom: 55px

  .title-container
    overflow: visible

  .work-packages--attachments
    margin-bottom: 25px
